import { h, Component } from 'preact';
import { observer } from 'mobx-preact';

import LineGraph from '../../components/lineGraph';
import wutil from '../../lib/wifet-util';

const MIN = 20;
const MAX = 60;

const initialState = {
	forcastData: wutil.makeRandomData(500, MIN, MAX),
	aiData: wutil.makeRandomData(500, MIN, MAX),
	windData: wutil.makeRandomData(500, MIN, MAX),
};

@observer
class Weather extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

	render() {
		const { windData, forcastData, aiData } = this.state;
		return (
			<div class='container-fluid mt-5 pt-2'>
				<div class='row'>
					<div class='col-12'>
						<h1>Weather</h1>
						<LineGraph 
							data={windData}
							data2={forcastData}
							data2B={aiData}
							legend='Wind'
							legend2='Wind forcast'
							legend2B='Wind AI prediction'
							width={1200}
							height={500}
							yMax={100}
							y2Max={100}
							showYTicks={1}
							yTicks='["20 m/s", "15 m/s", "10 m/s", "5 m/s", "0 m/s"]'
							showXTicks={1}
						/>
					</div>
				</div>
			</div>
		);
	}
}

export default Weather;
